<template>
    <div v-if="isDisplay != '4'" class="cours-result-shpi1-cours">
        <div class="cours-card-test">
            <!-- v-for="(cour, index) in cours" :key="index" -->
        </div>
    </div>
</template>

<script>
import axios from 'axios';

export default {

    components: {
    },
    data() {
        return {
            cours: []
        }
    },
    props: {
        state: String,
        isDisplay: String
    },
    methods: {
        allRecords: async function () {
            try {
                const response = await axios.get("http://localhost:5000/cours");
                let temp_array = response.data;
                this.cours = []

                let research = this.$route.query.research;
                let matter = this.$route.query.matiere;

                if (research) {
                    console.log("oui");
                }
                else if (matter) {
                    for (let i in temp_array) {

                        if ((temp_array[i].matiere == matter && temp_array[i].shpi == this.isDisplay)) {
                            let cour = JSON.parse(JSON.stringify(temp_array[i]));

                            if (cour.type == this.state) {
                                this.cours.push(cour);
                            }
                        }
                    }
                }
            } catch (err) {
                console.log(err);
            }
        },
    },
    watch: {
        state(){
            this.allRecords();
        }
    },
    mounted() {
        this.allRecords();
    }

}
</script>

<style lang="scss">
.cours-result-shpi1-cours {

    border-bottom: 5px solid var(--dark);
    margin-top: -5px;
    width: 100%;
    height: auto;
    display: flex;

}
</style>