<template>
  <div class="cours-result-page-container">
    <div class="cours-result-page-left-container">
      <div class="cours-result-matter-name-container">
        <h1>{{ matter }} :</h1>
      </div>
      <div class="cours-result-selectionned-type-container">
        <div class="cours-result-selectionned-cm" id="cours-result-cm" @click="isCourSelected = !isCourSelected"
          v-on:mouseenter="hover(true, 'cours-result-cm', isCourSelected)"
          v-on:mouseleave="hover(false, 'cours-result-cm', isCourSelected)">
          <p>Cours</p>
          <span class="material-icons">
            chevron_right
          </span>
        </div>
        <div class="cours-result-selectionned-td" id="cours-result-td" @click="isTdSelected = !isTdSelected"
          v-on:mouseenter="hover(true, 'cours-result-td', isTdSelected)"
          v-on:mouseleave="hover(false, 'cours-result-td', isTdSelected)">
          <p>Travaux Dirig√©s</p>
          <span class="material-icons">
            chevron_right
          </span>
        </div>
        <div class="cours-result-selectionned-fiches" id="cours-result-fiche"
          @click="isFicheSelected = !isFicheSelected"
          v-on:mouseenter="hover(true, 'cours-result-fiche', isFicheSelected)"
          v-on:mouseleave="hover(false, 'cours-result-fiche', isFicheSelected)">
          <p>Fiches</p>
          <span class="material-icons">
            chevron_right
          </span>
        </div>
        <div class="cours-result-selectionned-partiels" id="cours-result-partiel"
          @click="isPartielSelected = !isPartielSelected"
          v-on:mouseenter="hover(true, 'cours-result-partiel', isPartielSelected)"
          v-on:mouseleave="hover(false, 'cours-result-partiel', isPartielSelected)">
          <p>Partiels</p>
          <span class="material-icons">
            chevron_right
          </span>
        </div>
        <div class="cours-result-selectionned-tp" id="cours-result-tp" @click="isTpSelected = !isTpSelected"
          v-on:mouseenter="hover(true, 'cours-result-tp', isTpSelected)"
          v-on:mouseleave="hover(false, 'cours-result-tp', isTpSelected)">
          <p>Travaux Pratiques</p>
          <span class="material-icons">
            chevron_right
          </span>
        </div>
      </div>
    </div>
    <div class="cours-result-page-right-container">
      <div class="cours-result-shpi1-title-container" @click="is1aDeployed = !is1aDeployed">
        <p>SHPI 1A : </p>
        <span class="material-icons">
          {{ arrow1a }}
        </span>
      </div>
      <div v-if="is1aDeployed">
        <ContentContainerVue v-bind:state="state" v-bind:isDisplay="display1A"></ContentContainerVue>
      </div>
      <div class="cours-result-shpi2-title-container" @click="is2aDeployed = !is2aDeployed">
        <p>SHPI 2A : </p>
        <span class="material-icons">
          {{ arrow2a }}
        </span>
      </div>
      <div v-if="is2aDeployed">
        <ContentContainerVue v-bind:state="state" v-bind:isDisplay="display2A"></ContentContainerVue>
      </div>
      <div class="cours-result-shpi3-title-container" @click="is3aDeployed = !is3aDeployed">
        <p>Cycle Ing√©nieur 3A : </p>
        <span class="material-icons">
          {{ arrow3a }}
        </span>
      </div>
      <div v-if="is3aDeployed">
        <ContentContainerVue v-bind:state="state" v-bind:isDisplay="display3A"></ContentContainerVue>
      </div>
    </div>
  </div>
</template>

<script>
import ContentContainerVue from './components/ContentContainer.vue';

export default {

  components: {
    ContentContainerVue
  },
  data() {
    return {
      matter: 'Mati√®res',
      isCourSelected: true,
      isTdSelected: false,
      isFicheSelected: false,
      isPartielSelected: false,
      isTpSelected: false,
      arrow1a: "expand_more",
      arrow2a: "expand_more",
      arrow3a: "expand_more",
      state: "Cm",
      display1A: "1",
      display2A: "2",
      display3A: "3",
      is1aDeployed: true,
      is2aDeployed: true,
      is3aDeployed: true,
    }
  },
  methods: {
    styleSelectedCm: function (bool) {
      if (bool) {
        document.getElementById('cours-result-cm').style.transform = "scale(1.2)";
        document.getElementById('cours-result-cm').style.backgroundColor = "var(--dark)";
        document.getElementById('cours-result-cm').style.color = "white";
        document.getElementById('cours-result-cm').style.borderRadius = "15px 15px";
      } else {
        document.getElementById('cours-result-cm').style.transform = "scale(1)";
        document.getElementById('cours-result-cm').style.backgroundColor = "white";
        document.getElementById('cours-result-cm').style.color = "black";
        document.getElementById('cours-result-cm').style.borderRadius = "15px 0 0 15px";
      }
    },
    styleSelectedTd: function (bool) {
      if (bool) {
        document.getElementById('cours-result-td').style.transform = "scale(1.2)";
        document.getElementById('cours-result-td').style.backgroundColor = "var(--dark)";
        document.getElementById('cours-result-td').style.color = "white";
        document.getElementById('cours-result-td').style.borderRadius = "15px 15px";
      } else {
        document.getElementById('cours-result-td').style.transform = "scale(1)";
        document.getElementById('cours-result-td').style.backgroundColor = "white";
        document.getElementById('cours-result-td').style.color = "black";
        document.getElementById('cours-result-td').style.borderRadius = "15px 0 0 15px";
      }
    },
    styleSelectedTp: function (bool) {
      if (bool) {
        document.getElementById('cours-result-tp').style.transform = "scale(1.2)";
        document.getElementById('cours-result-tp').style.backgroundColor = "var(--dark)";
        document.getElementById('cours-result-tp').style.color = "white";
        document.getElementById('cours-result-tp').style.borderRadius = "15px 15px";
      } else {
        document.getElementById('cours-result-tp').style.transform = "scale(1)";
        document.getElementById('cours-result-tp').style.backgroundColor = "white";
        document.getElementById('cours-result-tp').style.color = "black";
        document.getElementById('cours-result-tp').style.borderRadius = "15px 0 0 15px";
      }
    },
    styleSelectedFiche: function (bool) {
      if (bool) {
        document.getElementById('cours-result-fiche').style.transform = "scale(1.2)";
        document.getElementById('cours-result-fiche').style.backgroundColor = "var(--dark)";
        document.getElementById('cours-result-fiche').style.color = "white";
        document.getElementById('cours-result-fiche').style.borderRadius = "15px 15px";
      } else {
        document.getElementById('cours-result-fiche').style.transform = "scale(1)";
        document.getElementById('cours-result-fiche').style.backgroundColor = "white";
        document.getElementById('cours-result-fiche').style.color = "black";
        document.getElementById('cours-result-fiche').style.borderRadius = "15px 0 0 15px";
      }
    },
    styleSelectedPartiel: function (bool) {
      if (bool) {
        document.getElementById('cours-result-partiel').style.transform = "scale(1.2)";
        document.getElementById('cours-result-partiel').style.backgroundColor = "var(--dark)";
        document.getElementById('cours-result-partiel').style.color = "white";
        document.getElementById('cours-result-partiel').style.borderRadius = "15px 15px";
      } else {
        document.getElementById('cours-result-partiel').style.transform = "scale(1)";
        document.getElementById('cours-result-partiel').style.backgroundColor = "white";
        document.getElementById('cours-result-partiel').style.color = "black";
        document.getElementById('cours-result-partiel').style.borderRadius = "15px 0 0 15px";
      }
    },
    hover: function (bool, id, isSelected) {
      if (!isSelected) {
        if (bool) {
          document.getElementById(id).style.transform = "scale(1.2)";
          document.getElementById(id).style.backgroundColor = "var(--dark)";
          document.getElementById(id).style.color = "white";
          document.getElementById(id).style.borderRadius = "15px 15px";
        } else {
          document.getElementById(id).style.transform = "scale(1)";
          document.getElementById(id).style.backgroundColor = "white";
          document.getElementById(id).style.color = "black";
          document.getElementById(id).style.borderRadius = "15px 0 0 15px";
        }
      }
    }
  },
  watch: {
    isCourSelected(newValue) {
      if (newValue) {
        this.styleSelectedCm(true);
        this.styleSelectedTd(false);
        this.styleSelectedTp(false);
        this.styleSelectedFiche(false);
        this.styleSelectedPartiel(false);
        this.isTdSelected = false;
        this.isFicheSelected = false;
        this.isPartielSelected = false;
        this.isTpSelected = false;
        this.state = "Cm";
      }
    },
    isTdSelected(newValue) {
      if (newValue) {
        this.styleSelectedTd(true);
        this.styleSelectedCm(false);
        this.styleSelectedTp(false);
        this.styleSelectedFiche(false);
        this.styleSelectedPartiel(false);
        this.isCourSelected = false;
        this.isFicheSelected = false;
        this.isPartielSelected = false;
        this.isTpSelected = false;
        this.state = "Td";

      }
    },
    isTpSelected(newValue) {
      if (newValue) {
        this.styleSelectedTp(true);
        this.styleSelectedTd(false);
        this.styleSelectedCm(false);
        this.styleSelectedFiche(false);
        this.styleSelectedPartiel(false);
        this.isCourSelected = false;
        this.isTdSelected = false;
        this.isFicheSelected = false;
        this.isPartielSelected = false;
        this.state = "Tp";

      }
    },
    isFicheSelected(newValue) {
      if (newValue) {
        this.styleSelectedFiche(true);
        this.styleSelectedTd(false);
        this.styleSelectedCm(false);
        this.styleSelectedTp(false);
        this.styleSelectedPartiel(false);
        this.isCourSelected = false;
        this.isTdSelected = false;
        this.isPartielSelected = false;
        this.isTpSelected = false;
        this.state = "Fiches";

      }
    },
    isPartielSelected(newValue) {
      if (newValue) {
        this.styleSelectedPartiel(true);
        this.styleSelectedTd(false);
        this.styleSelectedCm(false);
        this.styleSelectedTp(false);
        this.styleSelectedFiche(false);
        this.isCourSelected = false;
        this.isTdSelected = false;
        this.isFicheSelected = false;
        this.isTpSelected = false;
        this.state = "Partiels";

      }
    },
    is1aDeployed(newValue) {
      if (newValue) {
        this.arrow1a = "expand_more";
      } else {
        this.arrow1a = "chevron_right";
      }
    },
    is2aDeployed(newValue) {
      if (newValue) {
        this.arrow2a = "expand_more";
      } else {
        this.arrow2a = "chevron_right";
      }
    },
    is3aDeployed(newValue) {
      if (newValue) {
        this.arrow3a = "expand_more";
      } else {
        this.arrow3a = "chevron_right";
      }
    },
  },
  mounted() {
    this.styleSelectedCm(true);
    let matterSend = this.$route.query.matiere;
    if (matterSend) {
      this.matter = matterSend;
    }
    let year = this.$route.query.year;
    switch (year) {
      case "1":
        this.display1A = "1",
        this.display2A = "4";
        this.display3A = "4";
        break;
      case "2":
        this.display2A = "2",
        this.display1A = "4";
        this.display3A = "4";
        break;
      case "3":
        this.display3A = "3",
        this.display2A = "4";
        this.display1A = "4";
        break;
      case "0":
        this.display3A = "3",
        this.display2A = "2";
        this.display1A = "1";
        break;
    }
  }
}
</script>

<style lang="scss">
.cours-result-page-container {
  height: 100%;
  width: 100%;
  display: flex;
  background-image: url('../../../assets/2.svg');
  background-size: cover;
  background-repeat: no-repeat;
  align-items: center;

  .cours-result-page-left-container {

    height: calc(100% - 5vmin);
    width: 20%;
    margin-left: 2.5vmin;
    background-color: white;
    box-shadow: 0 8px 12px 0 rgba(57, 17, 69, .2);
    border-radius: 15px 0 0 15px;
    border: 5px solid var(--dark);

    .cours-result-matter-name-container {
      width: 100%;
      height: 10%;
      border-bottom: 5px solid var(--dark);
      display: flex;
      justify-content: center;
      align-items: center;

      h1 {
        height: calc(var(--text-size-card-title) + 5px);
        width: auto;
        margin: 0;
        line-height: 100%;
        font-size: var(--text-size-card-title);
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }

    .cours-result-selectionned-type-container {
      width: 100%;
      height: 90%;
      display: flex;
      align-items: flex-end;
      flex-direction: column;

      .cours-result-selectionned-cm,
      .cours-result-selectionned-fiches,
      .cours-result-selectionned-partiels,
      .cours-result-selectionned-tp,
      .cours-result-selectionned-td {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        width: 80%;
        height: 5%;
        margin-top: 10px;
        font-size: var(--text-size-table-content);
        border-radius: 15px 0 0 15px;
        border: 5px solid var(--dark);
        border-right: none;
        cursor: pointer;
        transition: 0.5s;
      }
    }
  }

  .cours-result-page-right-container {

    background-color: white;
    height: calc(100% - 5vmin);
    width: calc(80% - 5vmin);
    border-radius: 0 15px 15px 0;
    border: 5px solid var(--dark);
    border-left: none;

    .cours-result-shpi1-title-container,
    .cours-result-shpi2-title-container,
    .cours-result-shpi3-title-container {
      width: 100%;
      height: 10%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      font-size: var(--text-size-button-content);
      border-bottom: 5px solid var(--dark);
      cursor: pointer;

      p {
        margin: 0;
        margin-left: 3%;
      }

      .material-icons {
        margin-right: 3%;
        font-size: calc(var(--text-size-button-content) + 10px);
      }

      background: linear-gradient(to right, black 25%, white 50%);
      background-size: 210% 100%;
      background-position: right bottom;
      transition: all .5s ease-out;

      &:hover {
        background-position: left bottom;

        p {
          color: white;
        }
      }
    }
  }

}
</style>